.start {
  cursor: pointer;
  border: none;
  margin: 10px 0px 0px;
  padding: 0px 10px;
  border-radius: 4px;
  box-shadow: rgb(235 235 235) 0px 6px 0px;
  font-size: 35px;
  height: 55px;
  color: rgb(219, 82, 77);
  font-weight: bold;
  width: 150px;
  background-color: white;
  transition: color 0.5s ease-in-out 0s;
}
.next {
  width: calc((100% - 200px) / 2 + 5%);
  position: absolute;
  right: 5px;
  bottom: 0px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.nextIcon {
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background: none;
  border: none;
  width: 24px;
  height: 24px;
  align-items: center;
  justify-content: center;
  pointer-events: none;
  transition: all 0.2s ease-out 0s;
}
